import React, { Component, useState } from 'react';

import './header.css';

import Logo from '../logo/logo';
import NavBar from '../nav-bar/nav-bar';
import Hamburger from './hamburger/hamburger';

import logoWhite from '../../../assets/images/logo/logo-white.svg';
import { connect } from 'react-redux';
import * as actionType from '../../../store/actions/action-type'

const Header = (props) => {

  const [openNavBar, setopenNavBar] = useState(false);

  /* This function will toggle the nav-bar (close/open) */
  const toggleNavBar = () => {
    if (openNavBar) {
      setopenNavBar(false)
    } else {
      setopenNavBar(true)
    }
  }

  /* This function will be called whenever a nav-link will be clicked */
  const closeNavBar = () => {
    // console.log('closeNavBar is called');
    setopenNavBar(false)
  }

  const logOut = () => {
    props.logout()
    //setopenNavBar(false)
  }

  if (props.blog === true) {
    return null
  } else {
    return (
      <header className="header">
        <div className="container flex-row header-container">
          <Hamburger
            clicked={toggleNavBar}
            openNavBar={openNavBar}
          />
          <Logo url={logoWhite} width={40} height={40} id={"header-logo"} />
          <NavBar
            clicked={toggleNavBar}
            openNavBar={openNavBar}
            closeNavBar={closeNavBar}
            closeSubList={props.closeSubList}
            session={props.session.session}
            logout={logOut}
          />
        </div>
      </header>
    )
  }

}

const mapStateToProps = (state) => {
  return {
    session: state.login,
    blog: state.blog.contentOpen
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    logout: () => {
      sessionStorage.removeItem('jwt')
      dispatch({
        type: actionType.LOGOUT
      })
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps, null, { pure: false })(Header);